import React, { useEffect, useRef, useState, useCallback } from 'react';
import styled from 'styled-components';
import * as d3 from 'd3';

const GraphContainer = styled.div`
  width: 100%;
  height: 100%;
  position: relative;
  background: ${props => props.theme.primary};
  overflow: hidden;
`;

const MapSvg = styled.svg`
  width: 100%;
  height: 100%;
  cursor: default;

  g {
    will-change: transform;
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
  }
`;

const InfoPanel = styled.div`
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
  background: ${props => props.theme.secondary};
  border: 2px solid ${props => props.theme.border};
  border-radius: 12px;
  padding: 2rem;
  z-index: 100;
  min-width: 250px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  opacity: ${props => props.show ? 1 : 0};
  pointer-events: ${props => props.show ? 'auto' : 'none'};
  transition: opacity 0.3s ease;
`;

const InfoTitle = styled.h3`
  color: ${props => props.theme.text};
  margin: 0 0 1rem 0;
  font-size: 1.5rem;
  font-weight: 600;
  text-align: center;
`;

const InfoContent = styled.div`
  color: ${props => props.theme.textSecondary};
  font-size: 0.95rem;
  line-height: 1.6;
`;

const InfoItem = styled.div`
  margin-bottom: 0.75rem;
  padding: 0.5rem;
  background: ${props => props.theme.primary};
  border-radius: 6px;

  strong {
    color: ${props => props.theme.text};
    display: block;
    margin-bottom: 0.25rem;
  }
`;

const LegendPanel = styled.div`
  position: absolute;
  bottom: 20px;
  left: 20px;
  background: ${props => props.theme.secondary};
  border: 1px solid ${props => props.theme.border};
  border-radius: 8px;
  padding: 1rem;
  z-index: 100;
  min-width: 180px;
`;

const LegendTitle = styled.h4`
  color: ${props => props.theme.text};
  margin: 0 0 1rem 0;
  font-size: 1rem;
`;

const LegendItem = styled.div`
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
  font-size: 0.85rem;
  color: ${props => props.theme.textSecondary};
`;

const LegendColor = styled.div`
  width: 16px;
  height: 16px;
  border-radius: 3px;
  background: ${props => props.color};
`;

const CountyTooltip = styled.div`
  position: absolute;
  background: ${props => props.theme.secondary};
  border: 2px solid ${props => props.theme.border};
  border-radius: 10px;
  padding: 1rem;
  pointer-events: none;
  z-index: 500;
  color: ${props => props.theme.text};
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.5);
  opacity: ${props => props.show ? 1 : 0};
  transition: opacity 0.2s ease;
  min-width: 220px;
`;

const TooltipTitle = styled.div`
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 0.75rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid ${props => props.theme.border};
  color: ${props => props.theme.text};
`;

const TooltipStats = styled.div`
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
`;

const TooltipRow = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9rem;
  color: ${props => props.theme.textSecondary};
`;

const TooltipLabel = styled.span`
  display: flex;
  align-items: center;
  gap: 0.5rem;

  &::before {
    content: '';
    width: 10px;
    height: 10px;
    border-radius: 2px;
    background: ${props => props.color};
  }
`;

const TooltipValue = styled.span`
  font-weight: 600;
  color: ${props => props.theme.text};
`;

const TooltipTotal = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: 0.5rem;
  padding-top: 0.5rem;
  border-top: 1px solid ${props => props.theme.border};
  font-size: 1rem;
  font-weight: 700;
  color: ${props => props.theme.text};
`;

//  GeoJSON
const countyData = {
  "": { hotels: 28, gold: 10, silver: 12, bronze: 6, region: "north" },
  "": { hotels: 22, gold: 1, silver: 9, bronze: 12, region: "north" },
  "": { hotels: 18, gold: 5, silver: 6, bronze: 7, region: "south" },
  "": { hotels: 17, gold: 1, silver: 1, bronze: 15, region: "east" },
  "": { hotels: 15, gold: 0, silver: 6, bronze: 9, region: "south" },
  "": { hotels: 12, gold: 2, silver: 4, bronze: 6, region: "east" },
  "": { hotels: 10, gold: 3, silver: 3, bronze: 4, region: "east" },
  "": { hotels: 10, gold: 8, silver: 2, bronze: 0, region: "north" },
  "": { hotels: 9, gold: 1, silver: 3, bronze: 5, region: "south" },
  "": { hotels: 8, gold: 1, silver: 4, bronze: 3, region: "central" },
  "": { hotels: 7, gold: 4, silver: 2, bronze: 1, region: "south" },
  "": { hotels: 5, gold: 2, silver: 0, bronze: 3, region: "north" },
  "": { hotels: 5, gold: 0, silver: 2, bronze: 3, region: "central" },
  "": { hotels: 4, gold: 0, silver: 0, bronze: 4, region: "north" },
  "": { hotels: 4, gold: 1, silver: 0, bronze: 3, region: "island" },
  "": { hotels: 3, gold: 1, silver: 2, bronze: 0, region: "central" },
  "": { hotels: 3, gold: 2, silver: 0, bronze: 1, region: "south" },
  "": { hotels: 3, gold: 0, silver: 1, bronze: 2, region: "central" },
  "": { hotels: 3, gold: 2, silver: 0, bronze: 1, region: "island" },
  "": { hotels: 2, gold: 0, silver: 2, bronze: 0, region: "central" },
  "": { hotels: 1, gold: 0, silver: 1, bronze: 0, region: "north" },
  "": { hotels: 0, gold: 0, silver: 0, bronze: 0, region: "island" }
};

//  - 
const legendItems = [
  { color: '#2ecc71', label: ' (20)', range: '20+' },
  { color: '#3498db', label: ' (10-19)', range: '10-19' },
  { color: '#95a5a6', label: ' (5-9)', range: '5-9' },
  { color: '#7f8c8d', label: ' (<5)', range: '0-4' }
];

function GraphNetwork({ data, isVisible = false }) {
  const svgRef = useRef();
  const selectedPathRef = useRef(null); //  ref 
  const [hoveredCounty, setHoveredCounty] = useState(null);
  const [selectedCounty, setSelectedCounty] = useState(null);
  const [geoData, setGeoData] = useState(null);
  const [tooltipPosition, setTooltipPosition] = useState({ x: 0, y: 0 });
  const tooltipUpdateTimerRef = useRef(null);

  //  GeoJSON 
  useEffect(() => {
    //  GeoJSON 
    fetch('https://raw.githubusercontent.com/g0v/twgeojson/master/json/twCounty2010.geo.json')
      .then(response => response.json())
      .then(json => {
        //  GeoJSON
        json.features.forEach(feature => {
          const countyName = feature.properties.COUNTYNAME || feature.properties.name;
          if (countyData[countyName]) {
            feature.properties = {
              ...feature.properties,
              name: countyName,
              hotels: countyData[countyName].hotels,
              gold: countyData[countyName].gold,
              silver: countyData[countyName].silver,
              bronze: countyData[countyName].bronze,
              region: countyData[countyName].region
            };
          }
        });
        setGeoData(json);
      })
      .catch(error => {
        console.error('Error loading GeoJSON:', error);
        // 
        console.log('');
      });
  }, []);

  // 
  const getColor = useCallback((hotels) => {
    if (hotels >= 20) return '#2ecc71'; //  - 
    if (hotels >= 10) return '#3498db'; //  - 
    if (hotels >= 5) return '#95a5a6';  //  - 
    return '#7f8c8d'; //  - 
  }, []);

  useEffect(() => {
    // geoData SVG 
    if (!geoData || !isVisible || !svgRef.current) return;

    const svg = d3.select(svgRef.current);
    const width = svgRef.current.clientWidth;
    const height = svgRef.current.clientHeight;

    // 
    svg.selectAll("*").remove();

    //  GeoJSON bounds 
    const bounds = d3.geoBounds(geoData);
    const centerX = (bounds[0][0] + bounds[1][0]) / 2;
    const centerY = (bounds[0][1] + bounds[1][1]) / 2;

    const projection = d3.geoMercator()
      .center([centerX, centerY])
      .fitSize([width * 0.9, height * 0.9], geoData)
      .translate([width / 2, height / 2]);

    // 
    const path = d3.geoPath()
      .projection(projection)
      .pointRadius(1.5); // 

    // 
    const g = svg.append("g");

    // 
    let isZooming = false;
    const zoom = d3.zoom()
      .scaleExtent([0.8, 3])  // 
      .on("start", () => {
        isZooming = true;
      })
      .on("zoom", (event) => {
        //  CSS transform  SVG transform
        const { x, y, k } = event.transform;
        g.style("transform", `translate(${x}px, ${y}px) scale(${k})`);
        g.style("transform-origin", "0 0");
      })
      .on("end", () => {
        isZooming = false;
      });

    svg.call(zoom);

    // 
    const paths = g.selectAll("path")
      .data(geoData.features)
      .enter()
      .append("path")
      .attr("d", path)
      .attr("fill", d => getColor(d.properties.hotels || 0))
      .attr("stroke", "#263238")
      .attr("stroke-width", 1)
      .attr("vector-effect", "non-scaling-stroke") //  zoom  stroke 
      .style("cursor", "pointer")
      .style("shape-rendering", "geometricPrecision") // 
      .on("mouseenter", function(event, d) {
        // zoom  hover 
        if (isZooming) return;

        // 
        if (selectedPathRef.current !== this) {
          d3.select(this)
            .attr("fill", "#e74c3c")
            .attr("stroke-width", 2);
        }

        //  tooltip 
        const x = Math.min(event.pageX + 15, window.innerWidth - 260);
        const y = Math.max(event.pageY - 10, 50);
        setTooltipPosition({ x, y });
        setHoveredCounty(d.properties);
      })
      .on("mousemove", function(event, d) {
        //  throttle 
        if (tooltipUpdateTimerRef.current) return;

        tooltipUpdateTimerRef.current = setTimeout(() => {
          const x = Math.min(event.pageX + 15, window.innerWidth - 260);
          const y = Math.max(event.pageY - 10, 50);
          setTooltipPosition({ x, y });
          tooltipUpdateTimerRef.current = null;
        }, 16); //  60fps
      })
      .on("mouseleave", function(event, d) {
        // zoom 
        if (isZooming) return;

        //  throttle timer
        if (tooltipUpdateTimerRef.current) {
          clearTimeout(tooltipUpdateTimerRef.current);
          tooltipUpdateTimerRef.current = null;
        }

        // 
        if (selectedPathRef.current !== this) {
          d3.select(this)
            .attr("fill", getColor(d.properties.hotels || 0))
            .attr("stroke-width", 1);
        }
        setHoveredCounty(null);
      })
      .on("click", function(event, d) {
        // 
        if (selectedPathRef.current) {
          const prevData = d3.select(selectedPathRef.current).datum();
          d3.select(selectedPathRef.current)
            .attr("fill", getColor(prevData.properties.hotels || 0))
            .attr("stroke-width", 1);
        }

        // 
        d3.select(this)
          .attr("fill", "#f39c12")
          .attr("stroke-width", 2);

        selectedPathRef.current = this;
        setSelectedCounty(d.properties);
      });

    // Cleanup
    return () => {
      if (tooltipUpdateTimerRef.current) {
        clearTimeout(tooltipUpdateTimerRef.current);
      }
    };

  }, [geoData, getColor, isVisible]); //  isVisible 

  const displayCounty = hoveredCounty || selectedCounty;

  //  GeoJSON 
  if (!geoData || !isVisible) {
    return (
      <GraphContainer>
        <div style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          height: '100%',
          color: '#b0b8c4',
          fontSize: '1.2rem'
        }}>
          ...
        </div>
      </GraphContainer>
    );
  }

  return (
    <GraphContainer>
      <MapSvg ref={svgRef} />

      <CountyTooltip
        show={hoveredCounty !== null}
        style={{
          left: `${tooltipPosition.x}px`,
          top: `${tooltipPosition.y}px`
        }}
      >
        {hoveredCounty && (
          <>
            <TooltipTitle>
              {(hoveredCounty.name || hoveredCounty.COUNTYNAME || '').replace("", "")}
            </TooltipTitle>
            <TooltipStats>
              <TooltipRow>
                <TooltipLabel color="#FFD700"></TooltipLabel>
                <TooltipValue>{hoveredCounty.gold || 0} </TooltipValue>
              </TooltipRow>
              <TooltipRow>
                <TooltipLabel color="#C0C0C0"></TooltipLabel>
                <TooltipValue>{hoveredCounty.silver || 0} </TooltipValue>
              </TooltipRow>
              <TooltipRow>
                <TooltipLabel color="#CD7F32"></TooltipLabel>
                <TooltipValue>{hoveredCounty.bronze || 0} </TooltipValue>
              </TooltipRow>
            </TooltipStats>
            <TooltipTotal>
              <span></span>
              <span>{hoveredCounty.hotels || 0} </span>
            </TooltipTotal>
          </>
        )}
      </CountyTooltip>

      <InfoPanel show={displayCounty !== null}>
        {displayCounty && displayCounty.hotels !== undefined && (
          <>
            <InfoTitle>{(displayCounty.name || displayCounty.COUNTYNAME || '').replace("", "")}</InfoTitle>
            <InfoContent>
              <InfoItem>
                <strong></strong>
                {displayCounty.gold || 0} 
              </InfoItem>
              <InfoItem>
                <strong></strong>
                {displayCounty.silver || 0} 
              </InfoItem>
              <InfoItem>
                <strong></strong>
                {displayCounty.bronze || 0} 
              </InfoItem>
              <InfoItem style={{ background: 'rgba(52, 152, 219, 0.1)', fontWeight: 'bold' }}>
                <strong></strong>
                {displayCounty.hotels || 0} 
              </InfoItem>
              <InfoItem>
                <strong></strong>
                {displayCounty.region === 'north' ? '' :
                 displayCounty.region === 'central' ? '' :
                 displayCounty.region === 'south' ? '' :
                 displayCounty.region === 'east' ? '' : ''}
              </InfoItem>
            </InfoContent>
          </>
        )}
      </InfoPanel>

      <LegendPanel>
        <LegendTitle></LegendTitle>
        {legendItems.map((item, index) => (
          <LegendItem key={index}>
            <LegendColor color={item.color} />
            <span>{item.label}</span>
          </LegendItem>
        ))}
        <LegendItem style={{ marginTop: '0.75rem', paddingTop: '0.75rem', borderTop: '1px solid #37474f', fontWeight: 'bold', fontSize: '0.95rem' }}>
          <span>: 189 </span>
        </LegendItem>
      </LegendPanel>
    </GraphContainer>
  );
}

export default GraphNetwork;
